const getContent = (param: string | null): VariantContent => {
  if (!param) return VARIANT_CONTENT.default
  return VARIANT_CONTENT[param as keyof typeof VARIANT_CONTENT] || VARIANT_CONTENT.default
}

const getEnvWatchlistId = (env: string, content: VariantContent) => {
  switch (env) {
    case "internal":
    case "development":
      return content.dev
    case "staging":
      return content.stg
    case "production":
      return content.prod
    default:
      return content.prod
  }
}

const MobileInfluencerLoginSection = () => {
  const [searchParams] = useSearchParams()
  const param = searchParams.get("utm_content")
  const content = getContent(param)

  // Set watchlistId in sessionStorage for downstream use
  if (content && param) {
    sessionStorage.setItem("watchlistId", getEnvWatchlistId(environment.name, content))
  }

  return (
    <Stack alignItems="center" sx={{ width: "100%", mb: 2 }}>
      {content.heading}
    </Stack>
  )
}

export { MobileInfluencerLoginSection }
